function mainJs(){
    const mainDetail = document.querySelector('.mainDetail'),
        mainPic = document.querySelector('.mainPic'),
        mPerson = document.querySelector('.mPerson'),
        mainDetailOverImg = document.querySelectorAll('.mainDetailOver img'),
        mainDetailOverInfo = document.querySelectorAll('.mainDetailOverInfo img'),
        mainDetaiBtn = document.querySelectorAll('.mainDetaiBtn button'),
        mPlay2Info = document.querySelector('.mPlay2Info'),
        sign = document.querySelector('.mainSign');
    for(let i=0; i<mainDetaiBtn.length; i++){
        mainDetaiBtn[i].addEventListener('mouseenter',function(){
            mainPic.style.opacity = '0.3';
            mPerson.style.opacity = '0.3';
            mainDetail.style.opacity = '0.3';
            sign.style.opacity = '0.3';

            mainDetailOverImg[i].style.opacity = '1';
            mainDetailOverInfo[i].classList.add('active');
            
            if( mPlay2Info.classList.contains('active') ){
                mainDetaiBtn[7].style.opacity = '0'
            }
        })
        mainDetaiBtn[i].addEventListener('mouseleave',function(){
            mainPic.style.opacity = '1';
            mPerson.style.opacity = '1';
            mainDetail.style.opacity = '1';
            sign.style.opacity = '1';

            mainDetailOverImg[i].style.opacity = '0';
            mainDetailOverInfo[i].classList.remove('active');

            if( !mPlay2Info.classList.contains('active') ){
                mainDetaiBtn[7].style.opacity = '1'
            }
        })
    }
}
window.onload = mainJs;